.popup__btn-close {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(125%, -125%);
  padding: 0;
  margin: 0;
  border: none;
  background: none;
  font-size: 0;
}
@media screen and (max-width: 800px) {
  .popup__btn-close {
    transform: translate(0, -175%);
    width: 20px;
    height: 20px;
  }
}