.profile__name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin-right: 18px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media screen and (max-width: 918px) {
  .profile__name {
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    margin-right: 10px;
  }
}