.popup__btn-submit {
  width: 100%;
  height: 50px;
  padding: 0;
  margin: 0;
  background-color: #000;
  color: #fff;
  border-radius: 2px;
  border: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}
@media screen and (max-width: 800px) {
  .popup__btn-submit {
    font-size: 14px;
    line-height: 17px;
    height: 46px;
  }
}